import styled from "styled-components";

const Banner = styled.div`
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 60px;
  background-image: linear-gradient(
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.8)
    ),
    url("img/banner.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
`;

const Title = styled.h1`
  font-size: 68px;
  font-weight: 400;
`;

const Overview = styled.p`
  margin-top: 10px;
  font-size: 18px;
`;

function Home() {
  return (
    <Banner>
      <Title>OneNPay</Title>
      <Overview>
        더치페이를 쉽고 간편하게, OneNPay
        <br />
        친구들과 함께 즐겨보세요!
      </Overview>
    </Banner>
  );
}

export default Home;
