import styled from "styled-components";
import { motion } from "framer-motion";
import { Link, useMatch } from "react-router-dom";
import "../Style/Header.css";

const Nav = styled(motion.nav)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  background-color: white;
  width: 100%;
  top: 0;
  font-size: 14px;
  padding: 10px 40px;
`;

const Col = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled.svg`
  height: 30px;
  path {
    fill: rgb(13, 83, 33);
    stroke: rgb(255, 255, 255);
    stroke-width: 2;
  }
`;

const logoVariants = {
  normal: {
    fillOpacity: 1,
  },
  active: {
    fillOpacity: [0, 1, 0],
    transition: {
      repeat: Infinity,
    },
  },
};

const Title = styled.h1`
  color: rgb(13, 83, 33);
  font-size: 25px;
  font-family: "Anton", sans-serif;
  padding-left: 5px;
`;

const Items = styled.ul`
  display: flex;
  align-items: center;
  padding-left: 50px;
`;

const Item = styled.li`
  margin-right: 20px;
  font-size: 18px;
  font-weight: 500;
  transition: color 0.3s ease-in-out;
  position: relative;
  display: flex;
  color: #b8b4b4;
  justify-content: center;
  flex-direction: column;
  &:hover {
    color: rgb(13, 83, 33);
  }
`;

const Circle = styled(motion.span)`
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 2.5px;
  bottom: -5px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: rgb(13, 83, 33);
`;

const Btns = styled.div`
  display: flex;
  align-items: center;
  margin-right: 50px;
`;

const Btn = styled.button`
  background-color: white;
  font-size: 18px;
  font-family: "Source Sans Pro", sans-serif;
  border: 1px solid #e0e0e0d4;
  border-radius: 5px;
  width: 90px;
  margin-left: 15px;
`;

function Header() {
  const homeMatch = useMatch("/");
  const groupsMatch = useMatch("/groups");

  return (
    <Nav>
      <Col>
        <Logo xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512">
          <motion.path
            variants={logoVariants}
            whileHover="active"
            animate="normal"
            d="M535 41c-9.4-9.4-9.4-24.6 0-33.9s24.6-9.4 33.9 0l64 64c4.5 4.5 7 10.6 7 17s-2.5 12.5-7 17l-64 64c-9.4 9.4-24.6 9.4-33.9 0s-9.4-24.6 0-33.9l23-23L384 112c-13.3 0-24-10.7-24-24s10.7-24 24-24l174.1 0L535 41zM105 377l-23 23L256 400c13.3 0 24 10.7 24 24s-10.7 24-24 24L81.9 448l23 23c9.4 9.4 9.4 24.6 0 33.9s-24.6 9.4-33.9 0L7 441c-4.5-4.5-7-10.6-7-17s2.5-12.5 7-17l64-64c9.4-9.4 24.6-9.4 33.9 0s9.4 24.6 0 33.9zM96 64H337.9c-3.7 7.2-5.9 15.3-5.9 24c0 28.7 23.3 52 52 52l117.4 0c-4 17 .6 35.5 13.8 48.8c20.3 20.3 53.2 20.3 73.5 0L608 169.5V384c0 35.3-28.7 64-64 64H302.1c3.7-7.2 5.9-15.3 5.9-24c0-28.7-23.3-52-52-52l-117.4 0c4-17-.6-35.5-13.8-48.8c-20.3-20.3-53.2-20.3-73.5 0L32 342.5V128c0-35.3 28.7-64 64-64zm64 64H96v64c35.3 0 64-28.7 64-64zM544 320c-35.3 0-64 28.7-64 64h64V320zM320 352c53 0 96-43 96-96s-43-96-96-96s-96 43-96 96s43 96 96 96z"
          ></motion.path>
        </Logo>
        <Title>OneNPay</Title>
        <Items>
          <Item>
            <Link to="/">Home{homeMatch && <Circle layoutId="circle" />}</Link>
          </Item>
          <Item>
            <Link to="/groups">
              Groups{groupsMatch && <Circle layoutId="circle" />}
            </Link>
          </Item>
        </Items>
      </Col>
      <Col>
        <Btns>
          <Btn>로그인</Btn>
          <Btn>회원가입</Btn>
        </Btns>
      </Col>
    </Nav>
  );
}

export default Header;
